/* 💠 General */
.App {
  text-align: center;
}

.App-header {
  background: linear-gradient(135deg, #f6ffd7, #d8f9b0);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin);
  color: #222;
  width: 100%;
  padding: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); /* 🌟 Sombras épicas */
  border-radius: 15px;
}

/* 📌 Menú Superior */
.menu_superior {
  position: fixed;
  top: 0;
  left: 0;
  padding: 20px;
  width: 100vw;
  background: linear-gradient(90deg, #afe66d, #78c21f);
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.datos_menu_superior a {
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
  text-decoration: none;
  padding: 10px 15px;
  border-radius: 8px;
  transition: background 0.3s ease;
}

.datos_menu_superior a:hover {
  background: rgba(255, 255, 255, 0.3); /* 🔥 Efecto de brillo al pasar el mouse */
}

/* 🎨 Contenedores con bordes y efectos */
/* 📌 Contenedor de productos */
.contenedor {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr)); /* 🔥 Ajuste automático sin espacios vacíos */
  gap: 20px;
  justify-content: center;
  padding: 20px;
  width: 100%;
  max-width: 1200px; /* Limita el tamaño para evitar scroll lateral */
}

/* 🔹 Bloques de productos con diseño dinámico */
.bloque-productos {
  width: 100%;
  max-width: 260px;
  height: auto; /* 🌟 Ajusta la altura según el contenido */
  padding: 20px;
  background: linear-gradient(180deg, #d8f9b0, #afe66d);
  border-radius: 12px;
  border: 3px solid #78c21f;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* 🏆 Efecto al pasar el mouse */
.bloque-productos:hover {
  transform: scale(1.05);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.35);
}

.mod-precio{
  size: 5px;
  border: #222;
  color: #ffffff;
}

/* 🏆 Menú Inferior */
.menu-inferior {
  bottom: 0;
  left: 0;
  background: #78c21f;
  color: white;
  padding: 15px;
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.3);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

/* 🌟 Animaciones */
@keyframes glow {
  0% { box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); }
  50% { box-shadow: 0px 4px 15px rgba(255, 255, 255, 0.3); }
  100% { box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); }
}

.App-header:hover {
  animation: glow 2s infinite alternate;
}
